import Vue from 'vue'
import Router from 'vue-router'
const _import_ = file => () => import('@/pages/' + file + '.vue')
Vue.use(Router)
export default new Router({
  routes: [{
    path: '/',
    name: 'Home',
    component: _import_('home/home')
  },
  {
    path: '/city',
    name: 'City',
    component: _import_('city/city')
  },
  {
    path: '/detail/:id',
    name: 'Detail',
    component: _import_('detail/detail')
  }]
})



